@import url("https://fonts.googleapis.com/css2?family=Asap:wght@400;700&family=Raleway:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Asap:wght@400;700&family=Raleway:wght@700&display=swap");
.ButtonModal {
  width: 20%;
  background-color: #00904A;
  color: #FDFDFD;
  padding: 5px 0;
  outline: none;
  border: none;
  border-radius: 0.3em;
}

.WideButton {
  background-color: #00904A;
  color: #FDFDFD;
  padding: 5px 0;
  outline: none;
  border: none;
  border-radius: 0.3em;
  width: 90%;
}

#Modal {
  background-color: #FFFFFF;
  box-shadow: #000000 0 3px 6px 0.16;
  display: none;
}
#Modal #ModalTitle {
  font-family: "Raleway";
}
#Modal #ModalContent {
  font-family: "Asap";
}

.Display {
  display: grid !important;
  width: 90%;
}